.header{
    height: 50px;
    background-color: #1F2540;
    display: flex;
    padding: 15px 10px 15px 10px;
    align-items: center;
    justify-content: center;
}
.logoImage{
    display: flex;
}
.headerWrapper{
    display: flex;
    justify-content: space-between;
}
@media only screen and (max-width: 375px) {
    .logoImage{height: auto;width: 40vw;}
}